<template>
<div class="business">
    <div class="list">
      <ul class="list-ul">
          <li class="btm" v-for="(item,index) in getArr" :key="index" @click="selected(item)">
              <div class="img-content">
                  <div class="count">{{index}}</div>
                  <div class="imgbox">
                      <img :src="item.img1" class="img_food">
                  </div>
              </div>
              <div class="info">
                  <h4>{{item.name}}</h4>
                  <div class="desc">
                       <div>
                           <span class="star-wrapper">
                                <star :size="36" :score="item.score"></star>
                            </span>
                           <span>{{item.score}}分</span>
                           <span style="color: orangered">{{item.ratings.length}}条评论</span></div>
                      <span>{{item.address}}</span><br/>
                      <span>人均¥{{item.price}}</span>
                  </div>
              </div>
          </li>
      </ul>
    </div>
</div>
</template>
<script>
    import { mapGetters } from 'vuex'
    import star from "../star/star"
    export default {
        name: 'business',
        components: {
            star
        },
        data(){
          return {
              selectedFood:[]
          }
        },
        computed: {
            ...mapGetters([
                'getArr'
            ])
        },
        methods:{
            //传送店铺信息
            selected(food){
                this.selectedFood={};
                this.selectedFood=food;
                console.log(this.selectedFood);
                this.$emit("send-event",0);
                this.$emit("send-events",1);
                this.$store.commit({
                    type: 'Foods',
                    foodList3: this.selectedFood
                });
            }
        }
    }
</script>

<style lang="scss">
.business{
    width: 950px;
    height: 800px;
    .list{
        padding: 15px 20px 21px;
        background: #fff;
        border-radius: 4px;
        border: 1px solid #e5e5e5;
        color: #666;
        .list-ul{
            list-style: none;
            margin: 0;
            padding: 0;
            .btm{
                position: relative;
                border-bottom: 1px solid #e5e5e5;
                padding-bottom: 18px;
                padding-top: 12px;
                overflow: hidden;
                .img-content{
                    width: 220px;
                    height: 125px;
                    float: left;
                    position: relative;
                    border-radius: 4px;
                    cursor: pointer;
                    .count{
                        background: #ff4500;
                        border-radius: 2px;
                        position: absolute;
                        line-height: 13px;
                        width: 20px;
                        margin-left: -3px;
                        margin-top: 3px;
                        text-align: center;
                        font-size: 12px;
                        color: white;
                        z-index: 2000;
                    }
                    .imgbox{
                        display: block;
                        .img_food{
                            width: 100%;
                            height: 100%;
                        }
                    }
                }
                .info{
                    padding: 4px 0 0 20px;
                    max-width: 666px;
                    font-size: 12px;
                    margin-left: 20px;
                    float: left;
                    h4{
                        font-size: 16px;
                        line-height: 22px;
                        color: black;
                        cursor: pointer;
                    }
                    .desc{
                        line-height: 17px;
                        color: #666;
                        padding-top: 7px;
                        margin-bottom: 11px;
                        .star-wrapper{
                            padding: 2px 0;
                            text-align: center;
                        }
                        span{
                            cursor: pointer;
                            display: inline-block;
                            margin-top: 8px;
                        }
                    }
                }
            }
        }
    }
}
</style>