<template>
<div class="store">
    <div class="content">
        <div class="detail">
            <div class="d-left">
                <div class="name">
                    {{getFoods.name}}
                    <span @click="icon"><i class="iconfont icon-duihao2"></i>食品安全档案</span>
                </div>
                <div class="score">
                    {{getFoods.score}}分
                    <span>人均{{getFoods.price}}</span>
                </div>
                <div class="address">
                    <p> 地址:{{getFoods.address}}</p>
                    <p v-if="getFoods.tel"> 电话:{{getFoods.tel}}</p>
                    <p> 营业时间:{{getFoods.business}}</p>
                </div>
                <div class="tags">
                    <div>
                        <img v-if="getFoods.wifi" :src="getFoods.wifi" alt="">
                       <p v-if="getFoods.wifi">提供wifi</p>
                    </div>
                    <div>
                        <img v-if="getFoods.parking" align="left" :src="getFoods.parking" alt="">
                       <p v-if="getFoods.parking">停车场</p>
                    </div>
                </div>
            </div>
            <div class="d-right">
                <div class="big">
                    <div class="imgbox">
                        <img :src="getFoods.img1" alt="">
                    </div>
                    <div class="small">
                        <div class="smallimg" v-for="(item,index) in getFoods.images" :key="index" @click="toshow">
                               <img :src="item" alt="系统错误">
                        </div>
                    </div>
                </div>
                <div class="btmimg" v-if="shows">
                    <div class="headr" @click="closeimg">
                        <span class="iconfont icon-close"></span>
                    </div>
                    <el-carousel class="figure">
                        <el-carousel-item v-for="(item,index) in getFoods.images" :key="index" class="imgbox">
                            <img :src="item" alt="" class="itimg">
                        </el-carousel-item>
                    </el-carousel>
                </div>
            </div>
        </div>
        <div class="btm-cont">
            <div class="btm-left">
                <div class="recommends" v-if="getFoods.recommend">
                    <h3>推荐菜</h3>
                    <div class="cont">
                        <ul class="clear">
                            <li v-for="(item,index) in getFoods.foodimg" :key="index">
                                <div class="pic">
                                    <div class="imgbox">
                                        <img :src="item.img" alt="">
                                    </div>
                                    <div class="desc">
                                        <span>{{item.name}}</span>
                                        <b>¥{{item.price}}</b>
                                    </div>
                                </div>
                            </li>
                        </ul>
                        <div class="list">
                            <span v-for="(item,index) in getFoods.recommend" :key="index">{{item}}</span>
                        </div>
                    </div>
                </div>
                <div class="btm-contents">
                    <h3 v-if="getFoods.ratings" class="rate">{{getFoods.ratings.length}}条网友点评</h3>
                    <div class="total">
                        <div class="sort">
                            <span class="on">质量排序</span>
                            <span>时间排序</span>
                        </div>
                    </div>
                    <div class="com-cont">
                        <div class="sea">
                            <span>只看图片的评论</span>
                        </div>
                        <div>
                            <div class="list" v-for="(item,index) in getdata" :key="index">
                                <div class="header">
                                    <div class="imgbox">
                                        <img :src="item.avatar" alt="">
                                    </div>
                                </div>
                                <div class="info">
                                    <div class="name">{{item.username}}</div>
                                    <div class="date">
                                        <span>{{format(item.rateTime)}}</span>
                                    </div>
                                    <div class="source">
                                        <span class="star-wrapper">
                                                <star :size="36" :score="item.score"></star>
                                        </span>
                                    </div>
                                    <div class="desc">
                                       {{item.text}}
                                    </div>
                                    <div class="noShowBigImg" v-if="item.img.length>0">
                                        <div class="imgalls">
                                            <div class="thumbnall" v-for="(ite,index) in item.img" :key="index" @click="thumb(item)">
                                                <img :src="ite" alt="网络出错">
                                            </div>
                                        </div>
                                        <div class="block" >
                                            <el-carousel height="200px" v-if="num1==index" >
                                                <el-carousel-item v-for="(item,ind) in item.img" :key="ind" >
                                                    <img :src="item" alt="" class="itemimg" >
                                                </el-carousel-item>
                                            </el-carousel>
                                        </div>
                                    </div>
                                    <div class="reply" v-if="item.reply">
                                        商家回复:{{item.reply}}
                                    </div>
                                    <div class="like-cont">
                                        <div class="like" @click="dian(item,index)" :class="{liked:index==num}">
                                            <i class="iconfont icon-dianzan"></i>
                                            <span>赞</span>
                                        </div>
                                    </div>
                                    <div class="line"></div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
                <div class="block">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="pageSizes"
                        :page-size="pagesize"
                        layout="total, prev, pager, next"
                        :total="getFoods.ratings.length">  //这是显示总共有多少数据
                </el-pagination>
            </div>
            </div>
            <div class="btm-right">
                <recommend :advert="advert"></recommend>
            </div>
        </div>
    </div>
</div>
</template>
<script>
    import recommend from "../recommend/recommend"
    import star from "../star/star"
    import {formatDate} from "../js/formatDate"
    import { mapGetters } from 'vuex'
    export default {
        name: 'store',
        props:["advert"],
        components: {
            recommend,
            star
        },
        data(){
          return{
               num:100000,
              num1:10000,
              pageSizes: [5, 10, 15, 20],//这是下拉框可以选择的，每选择一行，要展示多少内容
              currentPage: 1,     //初始页
              pagesize: 5,
              shows:false
          }
        },
        methods:{
            // 每页条数变更
            handleSizeChange(val) {
                this.pagesize = val;
            },
            // 当前页码变更
            handleCurrentChange(val) {
                this.currentPage = val;
            },
            //显示轮播图
            toshow(){
                this.shows=true;
            },
            //关闭轮播图
            closeimg(){
                this.shows=false;
            },
            //点击食品档案
            icon(){
              alert("未登录不可查看食品安全档案")  ;
            },
            //评论轮播图
            thumb(it){
                var _this=this;
                console.log(2);
                this.getFoods.ratings.forEach(function (item,index) {
                    console.log(3);
                    if (it.username==item.username){

                        console.log(1);
                        _this.num1=index;

                    }
                });

            },
            //日期
            format(time) {
                let date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            },
            //点赞
            dian(it){
                     var _this=this;
                     this.getFoods.ratings.forEach(function (item,index) {
                         if (it.username==item.username){
                            _this.num=index;
                         }

                     });
            }
        },
        computed: {
            ...mapGetters([
                'getFoods'
            ]),
            //分页器数据筛选
            getdata(){
                let schArray=[];
                schArray = this.getFoods.ratings.slice((this.currentPage-1) * this.pagesize,this.currentPage * this.pagesize);
                return schArray;
            },
        },
        watch:{
            getdata(){
                this.count++;
            }
        }
    }
</script>

<style lang="scss">
