import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
      sellerlist: [],   //筛选的店铺信息
      schArr:[],        //分页信息
      selectedFood:{} //选择的店铺
  },
  mutations: {
      changeFoods(state, payload){
          state.sellerlist = payload.foodList;
      },
      changeArr(state, payload){
          state.schArr = payload.foodList2;
      },
      Foods(state, payload){
          state.selectedFood = payload.foodList3;
      }
  },
  actions: {
  },
  modules: {
  },
  getters: {
        getSells: state => {
          return state.sellerlist
        },
        getArr: state => {
            return state.schArr
        },
      getFoods: state => {
          return state.selectedFood
      }
    }
})
