<template>
<div class="filter">
    <div class="selected">
        <div class="hasselect" v-if="selected1.length > 0||selected2.length > 0">
            <span class="nor">已选条件</span>
            <span class="sel" v-for="(item,index) in selected1" :key="'o'+index" @click="sel1" >{{item.name}}x</span>
            <span class="sel" v-for="(item ,index) in selected2" :key="'p'+index" @click="sel2">{{item}}x</span>
            <span class="clearall" @click="clearall">.清除全部</span>
        </div>
        <div class="contclear">
            <div class="name">分类</div>
            <div class="all" :class="{allon1:active}" @click="alls">全部</div>
            <ul class="types">
                <li v-for="(item,index) in goods" :key="index" class="type" :class="{on1:num==index}" @click="selectedItem(item,index)">
                    <span>{{item.name}}</span>
                </li>
            </ul>
        </div>
        <div class="region">
            <div class="reg">区域</div>
            <div class="all" :class="{allon2:active2}" @click="alls2">全部</div>
            <ul class="places">
                <li v-for="(item,index) in places" :key="'s'+index" class="place" :class="{on2:num2==index}" @click="placed(index)">
                    <span >{{item}}</span>
                </li>
            </ul>
        </div>
    </div>
    <p style="color: white">{{showlist}}</p>
</div>
</template>
<script>
    export default {
        name: 'filtered',
        props:['goods','seller','places'],
        data(){
            return {
                active:true,
                num:3,
                num2:13,
                active2:true,
                selected1:[],
                selected2:[],
                count:0
            }
        },
        methods:{
            sel1(){
                this.selected1=[];
                this.count++;
            },
            sel2(){
                this.selected2=[];
                this.count++;
            },
            alls(){
                this.active=true;
                this.num=3;
                this.selected1=[];
                this.count++;
              },
            //分类存储
            selectedItem (item,index) {
                this.count++;
                this.selected1 = [];
                this.active=false;
                this.selected1.push(item);
                this.num=index;
            },
            alls2(){
                this.count++;
                this.active2=true;
                this.num2=13;
                this.selected2=[];
            },
            //地方存储
            placed(index){
                let _this=this;
                this.count++;
                this.active2=false;
                _this.selected2=[];
                this.num2=index;
                this.places.forEach(function (item,indexs) {
                    if(indexs==_this.num2){
                        _this.selected2.push(item);
                    }
                });
            },
            //清除选项
            clearall(){
                this.selected1=[];
                this.selected2=[];
                this.count++;
             }
        },
        computed:{
            //筛选数据
            sellerlist(){
                var str='';
                var array=[];
                var arr = [];
                this.selected1.forEach(function (item) {
                    arr.push(...item.stores);
                });
                this.selected2.forEach(function (item) {
                    str=item;
                });
                if(this.active){
                    this.seller.forEach(function (item) {
                        if(item.address.indexOf(str)!=-1) {
                            array.push(item);
                        }
                    })
                }else {
                    arr.forEach(function (item) {
                        if(item.address.indexOf(str)!=-1) {
                            array.push(item);
                        }
                    });
                }
                return array
            },
           //存储数据
            showlist(){
                this.$store.commit({
                    type: 'changeFoods',
                    foodList: this.sellerlist
                });

                return this.count;
            }
        }
    }
</script>

<style lang="scss">
.filter{
    position: relative;
    border: 1px solid #e5e5e5;
    margin-bottom: 14px;
    width: 950px;
    .selected{
       padding-left: 20px;
        .hasselect{
            cursor: pointer;
            height: 20px;
            padding: 10px 0 10px 20px;
            line-height: 20px;
            border-bottom: 1px solid #e5e5e5;
            font-size: 12px;
            .nor{
                font-size: 14px;
                margin-right: 18px;
            }
            .sel{
                border: 1px solid #13D1BE;
                color:#13D1BE ;
                height: 18px;
                margin-right: 10px;
                padding: 0 7px;
                border-radius: 10px;
            }
        }
         .contclear{
             overflow: hidden;
             padding: 10px 0 0;
             line-height: 20px;
             .name{
                 float: left;
                 width: 53px;
                 font-size: 14px;
                 text-align: center;
             }
             .all{
                 cursor: pointer;
                 float: left;
                 width: 53px;
                 font-size: 14px;
                 text-align: center;
             }
             .allon1{
                 background-color: lightseagreen;
                 color: white;
                 border-radius: 100px;
             }
             .types{
                 float: left;
                 width: 788px;
                 border-bottom:1px solid #e5e5e5 ;
                 .on1{
                     background-color: lightseagreen;
                     color: white;
                     border-radius: 100px;
                 }
                 .type{
                     cursor: pointer;
                     list-style: none;
                     float: left;
                     height: 28px;
                     line-height: 28px;
                     margin-left: 24px;
                     min-width: 55px;
                     font-size: 14px;
                     text-align: center;
                     span{
                         display: inline-block;
                         padding: 0 10px;
                     }
                 }
             }
         }
        .region{
            overflow: hidden;
            padding: 10px 0 0;
            line-height: 20px;
            .reg{
                float: left;
                width: 53px;
                font-size: 14px;
                text-align: center;
            }
            .all{
                float: left;
                width: 53px;
                cursor: pointer;
                font-size: 14px;
                text-align: center;
            }
            .allon2{
                background-color: lightseagreen;
                color: white;
                border-radius: 100px;
            }
            .places{
                float: left;
                width: 788px;
                border-bottom:1px solid #e5e5e5 ;
                .on2{
                    background-color: lightseagreen;
                    color: white;
                    border-radius: 100px;
                }
                .place{
                    cursor: pointer;
                    list-style: none;
                    float: left;
                    height: 28px;
                    line-height: 28px;
                    margin-left: 24px;
                    min-width: 55px;
                    font-size: 14px;
                    text-align: center;
                    span{
                        display: inline-block;
                        padding: 0 10px;
                    }
                }
            }
        }
    }
}
</style>