<template>
    <div class="block">
        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="pageSizes"
                :page-size="pagesize"
                layout="total, prev, pager, next"
                :total="getSells.length">  //这是显示总共有多少数据
        </el-pagination>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    export default {
        name:"pagination",
        props:["seller"],
        computed: {
            ...mapGetters([
                'getSells'
            ]),
            getdata(){
                let schArr=[];
                schArr = this.getSells.slice((this.currentPage-1) * this.pagesize,this.currentPage * this.pagesize);
                console.log(schArr);
                    this.$store.commit({
                        type: 'changeArr',
                        foodList2: schArr
                });
                return schArr;
            }
        },
        watch:{
          getdata(){
              this.count++;
          }
        },
        // 分页函数
        methods: {
            // 每页条数变更
            handleSizeChange(val) {
                this.pagesize = val;
            },
            // 当前页码变更
            handleCurrentChange(val) {
                this.currentPage = val;
            }
        },
        data() {
            return {
                pageSizes: [6, 12, 18, 24],//这是下拉框可以选择的，每选择一行，要展示多少内容
                currentPage: 1,     //初始页
                pagesize: 6,
            };
        }
    }
</script>
<style lang="scss">
    .block{
        text-align: center;
        margin-top: 240px;
    }
</style>